body {
  margin: 0;
  font-family: 'Nunito Sans',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F2FFDC;
  color: #2D2F36;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.origRadio {
  width: 0px;
  height: 0px;
  opacity: 0;
  position: absolute;
  z-index: -1;
}

.origRadio:checked+.custRadio::before {
  transition: opacity 0.5s ease;
  opacity: 1;
}

.custRadio {
  border-radius: 25px;
  padding: 10.5px 54px;
  position: relative;
  overflow: hidden;
  display: inline-flex;
  cursor: pointer;
}

.custRadio::before {
  content: '';
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #BEC7AF;
  z-index: -1;
  transition: opacity 0.5s ease;
}


.origCheck {
  width: 0px;
  height: 0px;
  opacity: 0;
  position: absolute;
  z-index: -1;
}

.origCheck:checked+.custCheck::before {
  transition: opacity 0.5s ease;
  opacity: 1;
}

.custCheck {
  border: 2px solid #828282;
  border-radius: 20px;
  padding: 8px 20px;
  position: relative;
  overflow: hidden;
  display: inline-flex;
  cursor: pointer;
}

.custCheck::before {
  content: '';
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #BEC7AF;
  z-index: -1;
  transition: opacity 0.5s ease;
}